import React from "react";
import { Typography, Paper } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useMsal, useAccount } from "@azure/msal-react";
import { MsalAuthenticationTemplate } from "@azure/msal-react";
import { AuthenticatedTemplate } from "@azure/msal-react";
import { InteractionType } from "@azure/msal-browser";
import Metadata from 'components/Metadata';
import { LoginPageMetadata } from 'configs/PageMetadata';
import Layout from 'layout/Layout';
import { loginRequest } from "configs/AuthConfig";
import { DisplayLoading } from "components/DisplayLoading";
import { DisplayError } from "components/DisplayError";


const useStyles = makeStyles(() => ({
    root: {
        margin: 20,
        padding: 20
    },
    sectionTitle: {
        paddingBottom: 10
    }
}));


export default function LoginPage() {
    const classes = useStyles();
    const { accounts } = useMsal();
    const account = useAccount(accounts[0] || {});
    const authRequest = {
        ...loginRequest
    };
    return (
        <Layout>
            <Paper elevation={3} className={classes.root} >
                <MsalAuthenticationTemplate
                    interactionType={InteractionType.Redirect}
                    authenticationRequest={authRequest}
                    errorComponent={DisplayError}
                    loadingComponent={DisplayLoading}
                >
                    <AuthenticatedTemplate>
                        { account &&
                            <Typography variant="h6">
                                Welcome {account.name}! You are successfully logged-in.
                            </Typography>
                        }
                    </AuthenticatedTemplate>

                </MsalAuthenticationTemplate>
            </Paper>
            <Metadata
                Title={LoginPageMetadata.Title}
                Description={LoginPageMetadata.Description}
            />
        </Layout>
    );
}
